<template>
    <span :style="`border-radius:${borderRadius}`" :class="[notificationCount ? 'newMessages--active': '', setColor()]" class="newMessages">{{notificationCount}}</span>
</template>
<script>
export default {
    props: {
        notificationCount: Number,
        color: String,
    },
    computed: {
        borderRadius: function(){
            const notsStrLen = String(this.notificationCount).length

            if(notsStrLen === 1){
                return '50%'
            }else if(notsStrLen === 2){
                return '45%'
            }else{
                return '40%'
            }
        },
    },
    methods: {
        setColor(){
            const colorStr = this.color

            if(colorStr == "red"){
                return 'newMessages--red'
            }else{
                return 'newMessages--grey'
            }
        },
    }
}
</script>
<style lang='scss' scoped>
@import '../assets/colors.scss';

.newMessages{
		padding: 1px 6px;

		pointer-events: none;

		color: white;
		font-size: 9px;
		font-weight: 700;

		display: none;
		justify-content: center;
		align-items: center;

		&--active{
			display: flex;
		}

		&--red{
			background-color: $notification-red;
		}
		&--grey{
			background-color: $notification-gray;
		}
	}
</style>